/**
 * @Author: sunshiqiang
 * @Date: 2018/8/13
 * @Title: 通过key获取url中的参数值
 */
import printErrorLog from './printErrorLog'
import typeCheck from './typeCheck'

const getParam = (key, url = window.location.href) => {
  try {
    let e = {}
    if (!typeCheck.isString(key)) {
      e = { name: 'getParam', message: '参数key非字符类型' }
    }
    if (!typeCheck.isString(url)) {
      if (e.message) {
        e.message = '参数key和url非字符类型'
      } else {
        e = { name: 'getParam', message: '参数url非字符类型' }
      }
    }
    if (Object.keys(e).length) {
      printErrorLog(e)
      return ''
    }
    let a = document.createElement('a')
    a.href = url
    let reg = new RegExp('[?&]' + key + '=([^&]+)')
    return a.search.match(reg) ? decodeURI(RegExp.$1) : ''
  } catch (e) {
    printErrorLog(e)
    return ''
  }
}

export default getParam
