/**
 * @Author: sunshiqiang
 * @Date: 2018/8/13
 * @Title: 从url中去除多个字段
 */
import printErrorLog from './printErrorLog'
import typeCheck from './typeCheck'
import parseURL from './parseURL'
import appendParams from './appendParams'

const removeParams = (arr, url = window.location.href) => {
  try {
    let e = {}
    if (!typeCheck.isArray(arr)) {
      e = { name: 'removeParams', message: '参数arr非数组' }
      return url
    } else {
      if (arr.findIndex(item => !typeCheck.isString(item)) !== -1) {
        e.message = { name: 'removeParams', message: '参数arr数组元素有非字符类型' }
      }
    }
    if (!typeCheck.isString(url)) {
      if (e.message) {
        e.message = e.message + ',参数url非字符类型'
      } else {
        e = { name: 'removeParams', message: '参数url非字符类型' }
      }
    }
    if (Object.keys(e).length) {
      printErrorLog(e)
      return url
    }
    const temp = parseURL(url)
    arr.forEach((item) => {
      delete temp.params[item]
    })
    return appendParams(temp.params, temp.originPath) + temp.hash
  } catch (e) {
    printErrorLog(e)
    return url
  }
}

export default removeParams
