/**
 * @Author: sunshiqiang
 * @Date: 2018/8/13
 * @Title: 把url的所有信息转化成json对象
 */
import printErrorLog from './printErrorLog'
import typeCheck from './typeCheck'

const parseURL = (url = window.location.href) => {
  try {
    if (!typeCheck.isString(url)) {
      printErrorLog({ name: 'parseURL', message: '参数url非字符类型' })
      return {}
    }
    let a = document.createElement('a')
    a.href = url
    return {
      source: url,
      protocol: a.protocol.replace(':', ''),
      hostname: a.hostname,
      port: a.port,
      search: decodeURI(a.search),
      origin: a.origin,
      params: (function () {
        let ret = {}
        let seg = a.search.replace(/^\?/, '').split('&')
        let len = seg.length
        let i = 0
        let s
        for (; i < len; i++) {
          if (!seg[i]) {
            continue
          }
          s = seg[i].split('=')
          ret[s[0]] = decodeURI(s[1])
        }
        return ret
      })(),
      file: decodeURI((a.pathname.match(/\/([^\/?#]+)$/i) || ['', ''])[1]),
      hash: decodeURI(a.hash),
      hashName: decodeURI(a.hash.replace('#', '')),
      pathname: decodeURI(a.pathname),
      originPath: decodeURI(a.origin + a.pathname),
      relative: decodeURI((a.href.match(/tps?:\/\/[^\/]+(.+)/) || ['', ''])[1]),
      segments: decodeURI(a.pathname).replace(/^\//, '').split('/')
    }
  } catch (e) {
    printErrorLog(e)
    return {}
  }
}

export default parseURL
